@font-face {
  font-family: 'klinic';
  src: url("../fonts/klinicslabmedium-webfont.eot");
  src: url("../fonts/klinicslabmedium-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/klinicslabmedium-webfont.woff2") format("woff2"), url("../fonts/klinicslabmedium-webfont.woff") format("woff"), url("../fonts/klinicslabmedium-webfont.ttf") format("truetype"), url("../fonts/klinicslabmedium-webfont.svg#klinic_slabmedium") format("svg");
  font-weight: 500;
  font-style: normal;
}
body {
  font-family: "Helvetica Neue", Helvetica, Arial, Geneva, sans-serif;
  overflow-x: hidden;
}
body .row {
  max-width: 72rem;
}
body .row.fullwidth {
  max-width: 100%;
}

h1 {
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 500;
  letter-spacing: 0.02em;
}

h2, h3, h4, h5 {
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 400;
  letter-spacing: 0.0125em;
}

.panel {
  font-size: 21px;
  padding: 13.3333333333px 0;
  width: 100%;
  position: relative;
}
@media only screen and (max-width: 40em) {
  .panel {
    padding: 10px 0;
  }
}
.panel.-blue {
  background-color: #3FB1E5;
  color: #fff;
}
@media only screen and (min-width: 40.063em) {
  .panel.-blue {
    padding-top: 20px;
  }
}
.panel.-blue h1, .panel.-blue h2, .panel.-blue p {
  color: #555555;
}
.panel.-gray {
  background-color: #F4F4F4;
}

.geo-shapes-title {
  margin: 0 auto;
}
.geo-shapes-title.blue h2 hr.border {
  border-color: #3FB1E5;
}
.geo-shapes-title.pink h2 hr.border {
  border-color: #FF2A53;
}
.geo-shapes-title h2 {
  display: inline-block;
  line-height: 1;
  padding: 0 1em;
  margin: 0 0 1.5em;
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 500;
  letter-spacing: 0.02em;
}
.geo-shapes-title h2 hr.border {
  border-color: #38BB4C;
  border-width: 0 0 5px;
  margin: 0.575em -5% 1em;
}
.geo-shapes-title h2 .geo-shapes {
  padding-bottom: 0;
  font-size: 21px;
  line-height: 1.5;
}
.geo-shapes-title h2 .geo-shapes hr {
  width: 42.5%;
  margin-left: auto;
  margin-right: auto;
}

.panel.panel--signoff {
  min-height: 320px;
  overflow: hidden;
  margin-top: 75px;
}
.panel.panel--signoff.panel--signoff-how {
  min-height: 570px;
}
.panel.panel--signoff .text-center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-top: -5%;
  z-index: 1200;
}
.panel.panel--signoff .text-center .geo-shapes {
  padding-top: 2em;
  padding-bottom: 0.5em;
}
.panel.panel--signoff .text-center h2 {
  margin: 0.5em 0;
}
.panel.panel--signoff .text-center h4 a {
  position: relative;
  display: inline-block;
  text-align: center;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  line-height: 1;
}
.panel.panel--signoff .text-center h4 a::after {
  display: inline-block;
  content: "";
  background: #FF2A53;
  height: 3px;
  width: 10%;
  opacity: 0;
  margin: -0.75em auto 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.panel.panel--signoff .text-center h4 a:hover::after {
  width: 100%;
  opacity: 1;
}
.panel.panel--signoff .text-center p:last-child {
  margin-bottom: 0;
}
.panel.panel--signoff .shape-position.shape-position-bottom {
  bottom: -10%;
}

.intro {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-top: 50px;
}
.intro .geo-shapes {
  padding-bottom: 0.5em;
}
.intro p {
  max-width: 92%;
  font-size: 0.875em;
  margin: 0.5em auto 1.5em;
}

.content .pink {
  color: #FF2A53;
  border-color: #FF2A53;
}
.content .blue {
  color: #3FB1E5;
  border-color: #3FB1E5;
}
.content .green {
  color: #38BB4C;
  border-color: #38BB4C;
}
.content h2, .content h3, .content h4 {
  color: #FF2A53;
  margin: 1.5em 0;
  line-height: 1.3;
}
.content h2 {
  margin: 2em 0 0.5em;
}
.content h2 .geo-shapes {
  padding: 0;
}
.content h3 {
  margin: 1em 0;
}
.content .pics p {
  padding-right: 0;
}
.content .text-center p {
  padding-right: 0;
}
.content p {
  padding-right: 5%;
  line-height: 1.636;
  margin-bottom: 1.5rem;
}
@media only screen and (min-width: 40.063em) {
  .content p:last-child {
    margin-bottom: 1em;
  }
}
.content p.lead {
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 400;
  letter-spacing: 0.0125em;
  padding: 0;
  font-size: 0.925em;
}
.content code,
.content pre {
  font-family: Consolas, "Lucida Console", Monaco, monospace;
  background: #f0f0f0;
  border: none;
  color: #464646;
  line-height: 1.8;
}
.content table {
  border: 2px solid #F4F4F4;
  margin: 1.5em auto;
  width: 100%;
}
.content table th,
.content table td {
  vertical-align: top;
  padding: 0.825em 1.5em 1.25em 0.75em;
  line-height: 1.333;
}
.content table th {
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 500;
  letter-spacing: 0.02em;
  color: white;
  font-size: 1em;
  background: #4E4E4E;
  line-height: 1.2;
}
.content table .small {
  font-size: 0.75em;
  line-height: 1.7;
  max-width: 50%;
}
.content dl {
  margin: 0 0 1.5em;
  font-size: 1.25rem;
  line-height: 1.5;
}
.content dl dt,
.content dl dd {
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 400;
  letter-spacing: 0.0125em;
}
.content dl dt {
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 500;
  letter-spacing: 0.02em;
  color: #c4c4c4;
}
.content dl.inline-list dt,
.content dl.inline-list dd {
  float: left;
  display: inline-block;
  margin-right: 1em;
}
.content dl.inline-list dt {
  clear: left;
}
.content address {
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 400;
  letter-spacing: 0.0125em;
  font-size: 1.25rem;
  margin: 2em 0 1em;
  line-height: 1.333;
}
.content address em {
  font-family: "klinic", Consolas, "Andale Mono", Rockwell, Courier, sans-serif;
  font-weight: 500;
  letter-spacing: 0.02em;
  font-style: normal;
  color: #c4c4c4;
}
.content hr.blank {
  color: white;
  border-color: white;
}
.content a.ripple {
  position: relative;
  display: inline-block;
  text-align: center;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  display: inline;
}
.content a.ripple::after {
  display: inline-block;
  content: "";
  background: #FF2A53;
  height: 3px;
  width: 10%;
  opacity: 0;
  margin: -0.75em auto 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.content a.ripple:hover::after {
  width: 100%;
  opacity: 1;
}
.content a.ripple:after {
  position: absolute;
  left: 0;
  bottom: -0.2em;
}
.content .breakout-cta-row {
  background: white;
  -webkit-box-shadow: 0 1px 3px #dedede;
          box-shadow: 0 1px 3px #dedede;
  padding: 1em 1.25em;
  margin-bottom: 1.25em;
}
.content .breakout-cta-row p.left {
  font-size: 0.75em;
  padding: 0.5em 0;
  margin: 0;
  color: #787878;
}
@media only screen and (max-width: 40em) {
  .content .breakout-cta-row p.left,
  .content .breakout-cta-row button {
    float: none;
    text-align: center;
    margin: 0 auto;
  }
}
.content .centerer {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.hidden {
  display: none;
}

.off-canvas-wrap.move-left {
  position: fixed;
}

@media only screen and (max-width: 40em) {
  .navbar.top-bar {
    top: 0;
  }
}

#tos {
  padding-top: 100px;
}
@media only screen and (max-width: 40em) {
  #tos {
    padding: 80px 20px 20px 20px;
  }
}
